gico que se renueva mediante procesos naturales a un ritmo igual o superior a su tasa de utilización. Se obtiene de los flujos continuos o repetitivos que se producen en el entorno natural” (IPCC, 2014, p. 164). Así pues, las energías renovables provienen de la utilización de recursos naturales como la luz solar, el agua o el viento, y son casi inagotables, por la cantidad de energía que contienen y su capacidad de regenerarse de forma natural, en contraste con las fuentes no renovables, cuyas reservas se agotan en el tiempo. Cabe destacar que, por no depender de combustibles fósiles, las fuentes de energía renovable generan menos emisiones de gases efecto invernadero, por tanto, contribuyen con la reducción del calentamiento global.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-md-11 col-lg-10 mb-4 mb-md-0",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"tarjeta color-primario p-3"},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_c('div',{staticClass:"col-3 col-sm-2 col-lg-1"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/video.svg")}})]),_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_c('div',{staticClass:"col mb-3 mb-sm-0"},[_c('h5',[_vm._v("Energías renovables en beneficio del planeta")]),_c('p',{staticClass:"text-small"},[_vm._v("Los principales tipos de energía renovable son energía solar, eólica, hídrica, geotérmica, mareomotriz y del oleaje, y la bioenergía (biomasa). ")])]),_c('div',{staticClass:"col-sm-auto"},[_c('a',{staticClass:"boton color-acento-botones",attrs:{"href":"https://www.youtube.com/watch?v=dLNCev0RMcQ","target":"_blank"}},[_c('span',[_vm._v("Ver video")]),_c('i',{staticClass:"fas fa-video"})])])])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div'